body {
  background-color: #f4f4f4;
  flex-direction: column;
  align-items: center;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.photo-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  flex: 1 0 23%;
  margin: 10px;
  transition: transform .2s ease-in-out;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.photo-card .info {
  background-color: #f8f8f8;
  padding: 10px;
}

.photo-card .info-item {
  margin: 0;
  padding: 5px 0;
}

.search-form {
  margin: 20px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px 20px;
}

.load-more:hover {
  background-color: #45a049;
}

